.header {
    text-align: center;
    font-family: 'Manrope', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 16px;
    line-height: calc(22/16);
    color: #111321;

    @media screen and (min-width:$desktop) {
        font-size: 18px;
        line-height: calc(25/18);
    }
}

.header__container {
    position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-height: 81px;
}

.burger {
    position: absolute;
    
}

.nav__list,
div.toggle__box__large {
    display: none;
}

@media screen and (min-width:$tablet) {
    .header__container {
        min-height: 106px;
    }

    div.toggle__box__large {
        display: flex;
    }

    .nav__list {
        display: flex;
        margin-right: 32px;
    }

    .nav__item:not(:last-child) {
        margin-right: 44px;
    }
}

@media screen and (min-width:$desktop) {
    .nav__list {
        margin-right: auto;
    }

    .nav__item:not(:last-child) {
        margin-right: 69px;
    }
}

.nav__input::placeholder {
    font-family: 'Poppins', sans-serif;
    font-size: 12px;
    font-weight: 400;
    font-style: normal;
    line-height: calc(21/12);
    color: #111321;
    opacity: 0.4;
    letter-spacing: 0.02em;

    @media screen and (min-width:$tablet) {
        & ::placeholder {
            font-size: 14px;
            line-height: calc(24/14);
        }
    }
}

.logo {
    font-size: 24px;
    line-height: calc(24/33);
    color: #111321;

    @media screen and (min-width:$tablet) {
        margin-right: 48px;
        font-size: 32px;
        line-height: calc(44/32);
    }

    @media screen and (min-width:$desktop) {
        font-size: 42px;
        line-height: calc(57/42);
        margin-right: 102px;
    }
}

