.toggle__box {
    margin-left: 16px;
}

@media screen and (min-width:$tablet) {
    div.toggle__box {
        align-items: center;
        justify-content: center;
        max-width: 98px;
        margin-left: 0;
    }

    .svg__dark {
        margin-left: 8px;
    }

    .svg__light {
        margin-right: 8px;
    }
}

.icon-decktop {
    display: none;

    @media screen and (min-width:$desktop) {
        display: block;
        align-items: center;
        justify-content: center;
        margin-left: 0;
    }
}

.icon-tablet {
    @media screen and (min-width:$desktop) {
        display: none;
    }
}

@media screen and (min-width:$desktop) {
    div.toggle__box {
        align-items: center;
        justify-content: center;
        max-width: 148px;
        margin-left: 0;
    }

    .svg__dark {
        margin-left: 12px;
    }

    .svg__light {
        margin-right: 12px;
    }
}

.input__dark__theme::placeholder {
    color: #f4f4f4;
}


.burger-menu__caption {
    font-family: 'Manrope', sans-serif;
    font-style: normal;
    font-weight: 700;
    font-size: 24px;
    padding-left: 16px;
    margin-left: 0;
    line-height: calc(33/24);
}

