// burger


.js-open-menu {
    top: 28px;
    fill: #F5F5F5;
    padding: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    animation: burger 3000ms infinite 1000ms;


    @media screen and (min-width:$tablet) {
        display: none;
    }
}

.js-close-menu {

    fill: #F5F5F5;
    justify-content: center;
    background-color: transparent;
    align-items: center;
    padding: 0;
    animation: cross 3000ms infinite 1000ms;

    @media screen and (min-width:$tablet) {
        display: none;
    }
}

@keyframes burger {
    0% {
        transform: scale(1);
        opacity: 0.7;
    }

    50% {
        transform: scale(1.1);
        opacity: 0.9;
    }

    100% {
        transform: scale(1);
        opacity: 1;
    }

}

@keyframes cross {
    0% {
        transform: scale(1);
    }

    50% {
        transform: scale(1.45);
    }

    100% {
        transform: scale(1);
    }
}

.menu__nav {
    margin-bottom: auto;
    margin-left: 0;
    text-align: center;
    margin-top: 45px;
    justify-content: flex-start;
    margin-bottom: auto;
}