.menu-toggle {
    background-color: transparent;
    cursor: pointer;
    border: none;

    position: absolute;
    top: 28px;
    right: 16px;

    &__icon {
        stroke: $blackColor;
    }

    @media screen and (min-width: $tablet) {
        display: none;
    }
}

.burger-menu__wrapper {
    position: fixed;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    padding-top: 24px;
    padding-bottom: 18px;
    z-index: 100;

    transform: translateX(100%);
    transition: transform 250ms ease-in-out;

    &.is-open {
        transform: translateX(0);
    }
}

.mobile-menu {
    list-style-type: none;
    
    &__item {
        height: 44px;
        display: flex;

        &.current {
                color: #F4F4F4;
                background-color: $hoverColor;
            }
    }

    &__item:not(:last-child) {
        margin-bottom: 9px;
    }

    &__link {
        display: flex;
        align-items: center;
        text-decoration: none;

        font-size: 16px;
        line-height: 22px;
        margin-left: 24px;
    }

    &__icon {
        margin-right: 14px;
        border: 1px solid #F4F4F4;
        border-radius: 50;
    }
}

.burger-menu__caption {
    margin-bottom: 68px;
    margin-top: 0;
    margin-left: 24px;

    font-size: 24px;
    line-height: 33px;

    
}

